import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import './App.css';
import Container from './contaner';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="App">
            <Container/>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;



